* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.nav-img {
  width: 100px;
}

.searchbar-container {
  display: flex;
  margin: 0 auto;
  padding: 0 20px;
  align-items: center;
}

.searchbar {
  background-color: #FFF;
  margin-right: 20px;
}

.searchbar input {
  padding: 10px;
  box-shadow: 0 2px 2px (0,0,0,0.25);
  border-radius: 4px;
  border: none;
}

.searchbar-btn button {
  background-color: #0e6f9f;
  border: none;
  border-radius: 10px;
  height: 44px;
  color: white;
  padding: 10px 12px;
}

.pokedex-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
}

.pokedex-grid {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);
}

.pokemon-card {
  display: flex;
  box-shadow: 4px 4px 4px (0,0,0,0.25);
  border-radius: 3px;
}

.pokemon-image {
  width: 70px;
  height: 70px;
  margin-right: 10px;
}
.pokemon-image-container {
  padding: 0 5px
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 10px 10px 0;
  flex: 1;
}

.card-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card-top h3 {
  text-transform: capitalize;

}

.card-bottom {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.pokemon-type {
  display: flex;
  gap: 10px
}

.pokemon-type-text {
  text-transform: capitalize;
}

.pokemon-heart-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s;
  -webkit-box-shadow: 2px 8px 15px 0px rgba(0,0,0,0.24); 
box-shadow: 2px 8px 15px 0px rgba(0,0,0,0.24);
}

.pokemon-heart-btn svg {
  width: 15px;
  height: 15px;
}

.pokemon-heart-btn svg path{
  transition: all 0.2s;

}
.pokemon-heart-btn:hover,
.pokemon-heart-btn svg:hover path {
  background-color: rgb(237,28,36);
  fill: white
}

.pagination-container {
  display: flex;
  align-items: center;
  gap: 8px;
}


.App {
  text-align: center;
}